.custom {
  padding: 90px 0;

  @include tablet {
    padding-top: 65px;
    padding-bottom: 65px;
  }

  @include mobile {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  h1 {
    font-weight: bold;
    font-size: 36px;
    line-height: 42px;

    @include tablet {
      font-size: 30px;
      line-height: 36px;
    }

    @include mobile {
      font-size: 27px;
      line-height: 33px;
    }
  }

  h2 {
    font-weight: bold;
    font-size: 33px;
    line-height: 39px;

    @include tablet {
      font-size: 30px;
      line-height: 36px;
    }

    @include mobile {
      font-size: 24px;
      line-height: 29px;
    }
  }

  h3 {
    font-weight: bold;
    font-size: 30px;
    line-height: 36px;

    @include tablet {
      font-size: 24px;
      line-height: 29px;
    }

    @include mobile {
      font-size: 20px;
      line-height: 24px;
    }
  }

  p,
  ul,
  ol {
    margin: 26px 0;
  }

  li {
    color: #1c1c1c;
    font-size: 16px;
    line-height: 19px;

    @include mobile {
      font-size: 14px;
      line-height: 16px;
    }

    &:not(:first-child) {
      margin-top: 15px;
    }

    &::marker {
      color: #37a58c;
    }
  }

  ul,
  ol {
    list-style: inside;
  }

  img {
    margin: 30px 0;
  }

  a {
    text-decoration: underline;
    color: inherit;

    &:hover {
      color: inherit;
      text-decoration: none;
    }

    &:visited {
      color: inherit;
    }
  }

  &--dark {
    color: #ffffff;
    background-color: rgba(2, 9, 40, 0.7);

    h1,
    h2,
    h3,
    h4,
    p,
    ul,
    ol,
    li,
    a {
      color: #ffffff;
    }

    li {
      &::marker {
        color: #37a58c;
      }
    }
  }

  &--light {
    background-color: #fafafa;
  }
}
.iscomplete {
  border-bottom: 1px solid #c4c4c4;
}
