.ddcommon {
  position: relative;
  display: -moz-inline-stack;
  zoom: 1;
  display: inline-block;
  *display: inline;
  cursor: default;
}
.ddcommon ul {
  padding: 0;
  margin: 0;
}
.ddcommon ul li {
  list-style-type: none;
}

.ddcommon .disabled img,
.ddcommon .disabled span,
.ddcommon.disabledAll {
  opacity: 0.5; /* standard: ff gt 1.5, opera, safari */
  -ms-filter: "alpha(opacity=50)"; /* ie 8 */
  filter: alpha(opacity=50); /* ie lt 7 */
  -khtml-opacity: 0.5; /* safari 1.x */
  -moz-opacity: 0.5; /* ff lt 1.5, netscape */
  color: #999999;
}
.ddcommon .clear {
  clear: both;
}
.ddcommon .shadow {
  -moz-box-shadow: 5px 5px 5px -5px #888888;
  -webkit-box-shadow: 5px 5px 5px -5px #888888;
  box-shadow: 5px 5px 5px -5px #888888;
}
.ddcommon input.text {
  color: #7e7e7e;
  padding: 0 0 0 0;
  position: absolute;
  background: #fff;
  display: block;
  width: 98%;
  height: 98%;
  left: 2px;
  top: 0;
  border: none;
}
.ddOutOfVision {
  position: relative;
  display: -moz-inline-stack;
  display: inline-block;
  zoom: 1;
  *display: inline;
}
.borderRadius .shadow {
  -moz-box-shadow: 5px 5px 5px -5px #888888;
  -webkit-box-shadow: 5px 5px 5px -5px #888888;
  box-shadow: 5px 5px 5px -5px #888888;
}
.borderRadiusBtm .shadow {
  -moz-box-shadow: -5px -5px 5px -5px #888888;
  -webkit-box-shadow: -5px -5px 5px -5px #888888;
  box-shadow: -5px -5px 5px -5px #888888;
}

img.fnone {
  float: none !important;
}
.ddcommon .divider {
  width: 0;
  height: 100%;
  position: absolute;
}
.ddcommon .ddArrow {
  display: inline-block;
  position: absolute;
  top: 50%;
  right: -13px;
  transform: translateY(50%);
  pointer-events: none;
}
.ddcommon .ddArrow:hover {
  background-position: 0 100%;
}
.ddcommon .ddTitle {
  padding: 0;
  position: relative;
  display: inline-block;
  width: 100%;
}
.ddcommon .ddTitle .ddTitleText {
  display: block;
}
.ddcommon .ddTitle .ddTitleText .ddTitleText {
  padding: 0;
}
.ddcommon .ddTitle .description {
  display: block;
}
.ddcommon .ddTitle .ddTitleText img {
  position: relative;
  vertical-align: middle;
  float: left;
}
.ddcommon .ddChild {
  position: absolute;
  display: none;
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  zoom: 1;
  z-index: 9999;
}
.ddcommon .ddChild li {
  clear: both;
}
.ddcommon .ddChild li .description {
  display: block;
}
.ddcommon .ddChild li img {
  border: 0 none;
  position: relative;
  vertical-align: middle;
  float: left;
}
.ddcommon .ddChild li.optgroup {
  padding: 0;
}
.ddcommon .ddChild li.optgroup .optgroupTitle {
  padding: 0 5px;
  font-weight: bold;
  font-style: italic;
}
.ddcommon .ddChild li.optgroup ul li {
  padding: 5px 5px 5px 15px;
}
.ddcommon .noBorderTop {
  border-top: none 0 !important;
  padding: 0;
  margin: 0;
}

/*************** default theme **********************/
.dd {
  width: 100% !important;
  // padding: 15px 35px;
  border-bottom: 1px solid #e5e5e5;
  margin-top: 20px;
}
.dd .divider {
  // border-left: 1px solid #c3c3c3;
  right: 0;
  width: 50px;
  cursor: pointer;

  @media (max-width: 1024px) {
    width: 40px;
  }
}
.dd .ddArrow {
  width: 20px;
  height: 10px;
  margin-top: -8px;
  margin-right: 10px;
  background: url(../img/hero/dd-arrow.svg) no-repeat;

  @media (max-width: 1024px) {
    margin-top: -11px;
    margin-right: 22px;
  }
}
.dd .ddArrow:hover {
  background-position: 0 100%;
}
.dd .ddTitle {
  color: #000;
}
.dd .ddTitle .ddTitleText {
  padding: 15px 0;

  @media (max-width: 1024px) {
    padding: 12px 40px 12px 0;
    display: flex;
    align-items: center;
  }
}
.dd .ddTitle .ddTitleText .ddTitleText {
  padding: 0;
}
.dd .ddTitle .description {
  font-size: 14px;
  color: #666;
}
.dd .ddTitle .ddTitleText img {
  padding-right: 5px;
}
.dd .ddChild {
  border: 1px solid #c3c3c3;
  left: -1px;
  top: 50px !important;
}
.dd .ddChild li {
  padding: 5px;
  background-color: #fff;
  border-bottom: 1px solid #c3c3c3;
  padding: 15px 36px;

  @media (max-width: 1024px) {
    padding: 13px 25px;
  }
}
.dd .ddChild li .description {
  color: #666;
}
.dd .ddChild li .ddlabel {
  color: #5f5f5f;
  opacity: 1;
}
.ddlabel {
  font-size: 16px;
  line-height: 19px;
  color: #5f5f5f;

  @media (max-width: 1024px) {
    font-size: 14px;
    line-height: 16px;
  }
}
.dd .ddChild li.hover {
  background-color: #f3f3f3;
}
.dd .ddChild li img {
  padding: 0 6px 0 0;
}
.dd .ddChild li.optgroup {
  padding: 0;
}
.dd .ddChild li.optgroup .optgroupTitle {
  padding: 0 5px;
  font-weight: bold;
  font-style: italic;
}
.dd .ddChild li.optgroup ul li {
  padding: 5px 5px 5px 15px;
}
.dd .ddChild li.selected {
  background-color: #d5d5d5;
  color: #000;
}

.ddcommon {
  .ddChild {
    height: 206px !important;

    @media (max-width: 1024px) {
      height: 190px !important;
    }
  }
}
